import React from 'react'
import {Card,Accordion,Button} from 'react-bootstrap'
import './myStyles.css'
import Img from '../static/Topslip2.jpg'
import Img2 from '../static/topslip.jpg'
import top from '../static/Topslip.jps.jpg'
import styled from 'styled-components'
const H1=styled.h1`
color:#000;
`

function TopSlip() {
    return (
        <div className="container">
            <div className="row">
                <div className="col-sm-12" id="topslip">
                <Accordion defaultActiveKey="0">
                    <Card>
                        <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="0">
                            <H1>TOPSLIP</H1>
                        </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="0">
                        <Card.Body>Hello! I'm the body</Card.Body>
                        </Accordion.Collapse>
                    </Card>
                    <Card>
                        <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="1">
                        Click to view Pictures
                        </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="1">
                        <Card.Body>
                            <img src={Img} fluid></img>
                            <img src={Img2} fluid></img>
                            <img src={top} fluid></img>
                        </Card.Body>
                        </Accordion.Collapse>
                    </Card>
                </Accordion>
                </div>
            </div>
        
        </div>
        
    )
}

export default TopSlip
