import React from 'react'
import {Card,Accordion,Button} from 'react-bootstrap'
import './myStyles.css'
import Nirar from '../static/nirar-dams.jpg' 
import Valparai from '../static/Vaalparai.jpg'
import road from '../static/roadValparai.jfif'
import styled from 'styled-components'
const H1=styled.h1`
color:#000;
`

function Vaalparai() {
    return (
        <div className="container">
            <div className="row">
                <div className="col-sm-12" id="topslip">
                <Accordion defaultActiveKey="0">
                    <Card>
                        <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="0">
                            <H1>VAALPARAI</H1>
                        </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="0">
                        <Card.Body>Hello! I'm the body</Card.Body>
                        </Accordion.Collapse>
                    </Card>
                    <Card>
                        <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="1">
                        Click to view Pictures
                        </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="1">
                        <Card.Body><img src={Nirar}/>
                        <img src={Valparai}/>
                        <img src={road}/>
                        </Card.Body>
                        </Accordion.Collapse>
                    </Card>
                </Accordion>
                </div>
            </div>
        
        </div>
    )
}

export default Vaalparai
