import React from 'react'
import {Card,Accordion,Button} from 'react-bootstrap'
import './myStyles.css'
import Aazhi from '../static/Aliyar-TopView.jpg'
import Aazhi1 from '../static/Azhiyar_Dam.jpg'
import monkey from '../static/monkey-falls.jpg'
import styled from 'styled-components'
const H1=styled.h1`
color:#000;
`

function Aazhiyar() {
    return (
        <div className="container">
            <div className="row">
                <div className="col-sm-12" id="topslip">
                <Accordion defaultActiveKey="0">
                    <Card>
                        <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="0">
                           <H1>AAZHIYAR</H1> 
                        </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="0">
                        <Card.Body>Hello! I'm the body</Card.Body>
                        </Accordion.Collapse>
                    </Card>
                    <Card>
                        <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="1">
                            Click to view Pictures
                        </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="1">
                        <Card.Body>
                            <img src={Aazhi}/>
                            <img src={Aazhi1}/>
                            <img src={monkey}/>
                        </Card.Body>
                        </Accordion.Collapse>
                    </Card>
                </Accordion>
                </div>
            </div>
        
        </div>
    )
}

export default Aazhiyar
